import type { ValueToTextRule } from '@ali/4ever-cangjie';
import { Inline } from '@ali/4ever-cangjie';
import Mention from '../models';

const AT = '@';

const valueToText: ValueToTextRule = {
  name: 'mention',

  match(node) {
    return Mention.isMention(node);
  },

  convert(node) {
    const name = (node as Inline).data.name || '';
    return AT + name;
  },
};

export default valueToText;
